import Color from 'color'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import { useQuery } from 'react-apollo-hooks'
import { ThemeProvider } from 'styled-components'

import { GThemeStyle } from '../graph/types'
import { ThemeStyleQuery } from '../models/ThemeModel.graphql'
import { useRoot } from './WithRoot'

interface IProps {
  branchId?: MaybeID
}

const ThemeSwitchProvider: React.FC<IProps> = ({ children, branchId }) => {
  const { business } = useRoot()
  const { data } = useQuery<GThemeStyle.Query, GThemeStyle.Variables>(
    ThemeStyleQuery,
    {
      variables: {
        brandId: branchId ? null : business.brandId,
        branchId: branchId || business.branchId,
      },
      suspend: false,
      skip: !business.isValid,
      fetchPolicy: 'cache-and-network',
    },
  )
  const theme = getTheme(data && data.theme && data.theme.styles)
  return useObserver(() => (
    <ThemeProvider theme={theme}>{children as React.ReactChild}</ThemeProvider>
  ))
}

export function getTheme(styles?: GThemeStyle.Styles) {
  // Fallback colors
  const defaultPrimary = '#54984a'
  const defaultSecondary = '#99230e'
  const defaultTertiary = '#d9a700'

  const defaultTheme = {
    primary: defaultPrimary,
    secondary: defaultSecondary,
    tertiary: defaultTertiary,
    secondaryHover: calcHoverColor(defaultPrimary),
    tertiaryHover: calcHoverColor(defaultTertiary),
    tertiaryText: 'white',
    unselected: '#E5E5E5',
    unselectedHover: '#EDEDED',
    unselectedText: '#5A5A5A',
    storeLogo: null,
    storeLogoMobile: null,
    backgroundCart: null,
  }

  if (styles) {
    return {
      ...defaultTheme,
      primary: styles.active || defaultPrimary,
      secondary: styles.main || defaultSecondary,
      tertiary: styles.mainSecondary || defaultTertiary,
      secondaryHover: styles.main && calcHoverColor(styles.main),
      tertiaryHover:
        styles.mainSecondary && calcHoverColor(styles.mainSecondary),
      storeLogo: styles.storeLogo,
      storeLogoMobile: styles.storeLogoMobile,
      backgroundCart: styles.backgroundCart,
    }
  }

  return defaultTheme
}

function calcHoverColor(inputColor: string) {
  return Color(inputColor)
    .lighten(0.2)
    .hsl()
    .string()
}

export type TTheme = ReturnType<typeof getTheme>

export default ThemeSwitchProvider
