import gql from 'graphql-tag'

export const ThemeStyleQuery = gql`
  query GThemeStyle($brandId: ID, $branchId: ID) {
    theme: applicationSettings(brand: $brandId, companyBranch: $branchId) {
      styles {
        active
        main
        mainSecondary
        storeLogo
        storeLogoMobile
        backgroundCart
      }
    }
  }
`
